import React, {useContext, useEffect, useReducer, useRef, useState} from "react";

import {ReactComponent as Close} from "./img/closeIcon.svg";
import {ReactComponent as Plus} from "./img/plusIcon.svg";
import {ReactComponent as DropDownIcon} from "./img/dropDownIcon.svg";

import {DropDownImport} from "./DropDown";

import './styles/Import.scss'
import {useDispatch, useSelector} from "react-redux";

import html2canvas from 'html2canvas';
import {jsPDF} from 'jspdf';

import * as XLSX from 'xlsx';
import {Data} from "../../Info/Types/Data";
import {ContexDropDown} from "../../Context/ContexDropDown";

export const ImportData: React.FC = () => {
  const [dataSaved, setDataSaved] = useState(false);

  const [fileName, setFileName] = useState('');
  const [parsedData, setParsedData] = useState<any[]>([]);

  const [nameValue, setNameValue] = useState('');
  const [nameValueOpen, setNameValueOpen] = useState(false);

  const [nameEnglishValue, setNameEnglishValue] = useState('');
  const [nameEnglishValueOpen, setNameEnglishValueOpen] = useState(false);

  const [categoryValue, setCategoryValue] = useState('');
  const [categoryValueOpen, setCategoryValueOpen] = useState(false);

  const handleOnChoseDropDownValue = (value: string, dropDownName: string) => {
    if (!value && !dropDownName) {
      setNameValueOpen(false);
    }

    switch (dropDownName) {
      case 'Наименование':
        setNameValue(value.trim())
        break;

      case 'Наименование на английском':
        setNameEnglishValue(value.trim())
        break;

      case 'Категория':
        setCategoryValue(value.trim())
        break;
    }
    // switch (dropDownName) {
    //   case ('buyingPrice'):
    //     setBuyingPriceValue(value);
    //     break;
    //   case ('sellingPrice') :
    //     setSellingPriceValue(value);
    //     break;
    //
    //   case ('unitType'):
    //     setUnitTypeValue(value);
    //     break;
    //
    //   case ('provider'):
    //     setProviderValue(value);
    //     break;
    //
    //   case ('unit'):
    //     setTypeValue(value);
    //     break;
    //
    //   case ('size'):
    //     setSizeValue(value);
    //     break;
    //
    //   case ('volume'):
    //     setVolumeValue(value);
    //     break;
    //
    //   case ('weight'):
    //     setWeightValue(value);
    //     break;
    //
    //   case ('minSellingPrice'):
    //     setMinSellValue(value);
    //     break;
    // }
  }

  const namesOfKeys = parsedData.length > 0 ? Object.keys(parsedData[0]) : [];

  const data = useSelector(state => state);
  const dispatch = useDispatch();

  const handleOnCloseImport = () => {
    dispatch({type: 'change-popUp-status', import: false})
  }

  const handleUpload = (e: any) => {
    setFileName(e.target.files[0].name)
    e.preventDefault();
    setNameValueOpen(false)
    setNameEnglishValueOpen(false)
    setCategoryValueOpen(false)

    const reader = new FileReader();

    reader.onload = function (e) {
      if (e.target) {
        const data = e.target.result;
        let readedData = XLSX.read(data, {type: 'binary'});
        readedData.SheetNames.forEach((sheetName) => {
          const xl_row_object = XLSX.utils.sheet_to_json(readedData.Sheets[sheetName]);
          setParsedData(xl_row_object)
        })
      }

    };
    reader.readAsBinaryString(e.target.files[0])
  }

  const popUpRef = useRef(null);
  const fileRef = useRef<any>(null);

  useEffect(() => {
    // if (exportData.length === 0) {
    //   const newData =
    //
    //     setExportData(newData);
    // }

    const handleOnClick = (e: any) => {
      if (e.target.className !== 'import-data') return;
      handleOnCloseImport();
    }

    document.addEventListener('click', handleOnClick, true);

    return () => {
      document.removeEventListener('click', handleOnClick, true);
    }
  }, [popUpRef])

  return (
    <>
      <input
        onChange={handleUpload}
        ref={fileRef}
        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" hidden
        type="file"
      />
      {dataSaved
        ? (
          <div className={'import-data'}>
            <div className={'import-data__container-result'}>
              <Close onClick={handleOnCloseImport} className={'import-data--close'}/>
                <p className={'import-data__container-result-text'}>Результат импорта</p>
              <div className={'import-data__container-result-body'}>
                <div className={'import-data__container-result-body-paragraph--wrapper'}>
                  <p className={'import-data__container-result-body-paragraph'}>Загружено позиций: <span className={'import-data__container-result-body-paragraph--green'}>{parsedData.length}</span></p>
                  <p className={'import-data__container-result-body-paragraph'}>Пропущено из-за ошибок: <span className={'import-data__container-result-body-paragraph--red'}>20</span></p>
                </div>

                <div>
                  <p className={'import-data__container-result-body-paragraph import-data__container-result-body-paragraph--blue'}>Отчёт об ошибках</p>
                  <p className={'import-data__container-result-body-paragraph import-data__container-result-body-paragraph--blue'}>Список незагруженных данных</p>
                </div>
                <div></div>
              </div>
            </div>
          </div>
        )
        : (
          <div ref={popUpRef} className={'import-data'}>
            <div className={'import-data__container'}>
              <Close onClick={handleOnCloseImport} className={'import-data--close'}/>
              <div className={'import-data__body'}>
                <section className={'import-data__header'}>
                  <article>
                    <h2 className={'import-data__title'}>Импорт товаров</h2>
                  </article>
                  <div className={'import-data__file'}>
                    <div className={'import-data__label'}>Прикреплённый Excel файл</div>
                    <div className={'import-data__file-details'}>
                      <div className={'import-data__file-name'}>{fileName}</div>
                      <div><Plus onClick={() => {
                        if (fileRef) {
                          fileRef.current.click()
                        }
                      }}/></div>
                    </div>
                  </div>
                  <p className={'import-data__header-info'}>Определите поля в соответствии с файлом</p>
                </section>
                <section className={'import-data__body-content--wrapper'}>
                  <section className={'import-data__body-left-side'}>
                    <div className={'import-data__left-side-first-group'}>
                      <article>
                        Наименование
                      </article>
                      <div onClick={() => setNameValueOpen(!nameValueOpen)} className={'import-data__informer'}>
                        <p>{nameValue}</p>
                        <div><DropDownIcon/></div>
                        {(namesOfKeys.length > 0 && nameValueOpen) &&
                          <ContexDropDown.Provider
                            value={{
                              handleOnClick: handleOnChoseDropDownValue
                            }}
                          >
                            <DropDownImport options={namesOfKeys} dropDownName={'Наименование'}/>
                          </ContexDropDown.Provider>

                        }

                      </div>
                    </div>
                    <div className={'import-data__left-side-first-group'}>
                      <article>
                        Наименование на английском
                      </article>
                      <div onClick={() => setNameEnglishValueOpen(!nameEnglishValueOpen)}
                           className={'import-data__informer'}>
                        <p>{nameEnglishValue}</p>
                        <div><DropDownIcon/></div>
                        {(namesOfKeys.length > 0 && nameEnglishValueOpen) &&
                          <ContexDropDown.Provider
                            value={{
                              handleOnClick: handleOnChoseDropDownValue
                            }}
                          >
                            <DropDownImport options={namesOfKeys} dropDownName={'Наименование на английском'}/>
                          </ContexDropDown.Provider>
                        }
                      </div>
                    </div>
                    <div className={'import-data__left-side-first-group'}>
                      <article>
                        Категория
                      </article>
                      <div onClick={() => setCategoryValueOpen(!categoryValueOpen)} className={'import-data__informer'}>
                        <p>{categoryValue}</p>
                        <div><DropDownIcon/></div>
                        {(namesOfKeys.length > 0 && categoryValueOpen) &&
                          <ContexDropDown.Provider
                            value={{
                              handleOnClick: handleOnChoseDropDownValue
                            }}
                          >
                            <DropDownImport options={namesOfKeys} dropDownName={'Категория'}/>
                          </ContexDropDown.Provider>
                        }
                      </div>
                    </div>
                    <div className={'import-data__left-side-second-group'}>
                      <div className={'import-data__left-side-second-group--left-container'}>
                        <div className={'import-data__left-side-second-group--left-container-text'}>
                          Цена закупки
                        </div>
                        <div className={'import-data__left-side-second-group--left-container-wrapper'}>
                          <div className={'import-data__left-side-second-group--left-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>

                          <div className={'import-data__left-side-second-group--left-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                      <div className={'import-data__left-side-second-group--left-container'}>
                        <div className={'import-data__left-side-second-group--right-container-text'}>
                          Цена продажи
                        </div>
                        <div className={'import-data__left-side-second-group--right-container-wrapper'}>
                          <div className={'import-data__left-side-second-group--right-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>

                          <div className={'import-data__left-side-second-group--right-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className={'import-data__left-side-third-group'}>
                      <div className={'import-data__left-side-third-group--left-container'}>
                        <div className={'import-data__left-side-third-group--left-container-text'}>
                          Арикул
                        </div>
                        <div className={'import-data__left-side-third-group--left-container-wrapper'}>
                          <div className={'import-data__left-side-third-group--left-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                      <div className={'import-data__left-side-third-group--left-container'}>
                        <div className={'import-data__left-side-third-group--right-container-text'}>
                          Ед. измерения
                        </div>
                        <div className={'import-data__left-side-third-group--right-container-wrapper'}>
                          <div className={'import-data__left-side-third-group--right-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className={'import-data__left-side-first-group'}>
                      <article>
                        Поставщик
                      </article>
                      <div className={'import-data__informer'}>
                        <p>123</p>
                        <div><DropDownIcon/></div>
                      </div>
                    </div>
                    <div className={'import-data__left-side-fourth-group'}>
                      <article>Габариты</article>
                      <div className={'import-data__left-side-fourth-group--wrapper-info'}>
                        <div className={'import-data__informer'}>
                          <p>123</p>
                          <div><DropDownIcon/></div>
                        </div>

                        <div className={'import-data__informer'}>
                          <p>123</p>
                          <div><DropDownIcon/></div>
                        </div>

                        <div className={'import-data__informer'}>
                          <p>123</p>
                          <div><DropDownIcon/></div>
                        </div>

                        <div className={'import-data__informer'}>
                          <p>123</p>
                          <div><DropDownIcon/></div>
                        </div>
                      </div>
                    </div>
                  </section>
                  <section className={'import-data__body-right-side'}>
                    <div className={'import-data__right-side-first-group'}>
                      <article>
                        Тип
                      </article>
                      <div className={'import-data__informer'}>
                        <p>123</p>
                        <div><DropDownIcon/></div>
                      </div>
                    </div>
                    <div className={'import-data__right-side-second-group'}>
                      <div className={'import-data__right-side-second-group--left-container'}>
                        <div className={'import-data__right-side-second-group--left-container-text'}>
                          Объем
                        </div>
                        <div className={'import-data__right-side-second-group--left-container-wrapper'}>
                          <div className={'import-data__right-side-second-group--left-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>

                          <div className={'import-data__right-side-second-group--left-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                      <div className={'import-data__right-side-second-group--left-container'}>
                        <div className={'import-data__left-side-second-group--right-container-text'}>
                          Масса
                        </div>
                        <div className={'import-data__left-side-second-group--right-container-wrapper'}>
                          <div className={'import-data__left-side-second-group--right-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>

                          <div className={'import-data__left-side-second-group--right-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className={'import-data__right-side-third-group'}>
                      <div className={'import-data__left-side-third-group--left-container'}>
                        <div className={'import-data__left-side-third-group--left-container-text'}>
                          Макс. скидка
                        </div>
                        <div className={'import-data__left-side-third-group--left-container-wrapper'}>
                          <div className={'import-data__left-side-third-group--left-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                      <div className={'import-data__left-side-third-group--left-container'}>
                        <div className={'import-data__left-side-third-group--right-container-text'}>
                          ПДВ/НДС
                        </div>
                        <div className={'import-data__left-side-third-group--right-container-wrapper'}>
                          <div className={'import-data__left-side-third-group--right-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className={'import-data__right-side-fifth-group'}>
                      <div className={'import-data__right-side-fifth-group--left-container'}>
                        <div className={'import-data__right-side-fifth-group--left-container-text'}>
                          Мин. цена продажи
                        </div>
                        <div className={'import-data__right-side-fifth-group--left-container-wrapper'}>
                          <div className={'import-data__right-side-fifth-group--left-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                      <div className={'import-data__right-side-fifth-group--left-container'}>
                        <div style={{opacity: 0}}
                             className={'import-data__right-side-fifth-group--right-container-text'}>
                          ПДВ/НДС
                        </div>
                        <div className={'import-data__right-side-fifth-group--right-container-wrapper'}>
                          <div className={'import-data__right-side-fifth-group--right-container-informer'}>
                            <p>123</p>
                            <div><DropDownIcon/></div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className={'import-data__right-side-seventh-group'}>
                      <article>
                        Описание
                      </article>
                      <div className={'import-data__informer'}>
                        <p>123</p>
                        <div><DropDownIcon/></div>
                      </div>
                    </div>
                    <div className={'import-data__container-button'}>
                      <button
                        onClick={() => {
                          const importData = parsedData.map(item => (
                            {
                              name: item[nameValue],
                              nameOnEnglish: item[nameEnglishValue],
                              category: item[categoryValue],
                              status: 'green',
                            }
                          ))
                          dispatch({type: 'add__new-product', newData: importData})
                          setDataSaved(true);
                        }}
                        className={'import-data__button'}
                      >
                        Сохранить
                      </button>
                    </div>
                  </section>
                </section>
              </div>
            </div>
          </div>
        )
      }
    </>
  )
}