import React, {useContext, useEffect, useRef} from "react";
import {ContexDropDown} from "../../../Context/ContexDropDown";
import {v4 as uuidv4} from "uuid";

import SimpleBarReact from "simplebar-react";
import "simplebar/src/simplebar.css";

type DropDownProps = {
  options: any[],
  dropDownName: string
}

export const DropDownImport: React.FC<DropDownProps> = ({options, dropDownName}) => {
  const {handleOnClick} = useContext(ContexDropDown);

  const ref = useRef(null);

  useEffect(() => {
    const handleOnClickOut = (e: any) => {
      if (e.target.className !== 'new-product__dropdown-item') handleOnClick('', '');
    }

    document.addEventListener('click', handleOnClickOut, true);

    return () => {
      document.removeEventListener('click', handleOnClickOut, true);
    }
  }, [])

  return (
    <>
      <div

        style={{
          position: options.length > 10 ? ('relative') : 'absolute',
        }}
        className="new-product__dropdown-menu"
      >
        <SimpleBarReact
          style={{
            maxHeight: '130px',
          }}
          forceVisible="y"
          autoHide={false}
          scrollbarMinSize={15}
        >
          {options.map((option: any, i) => (
            <div key={uuidv4()} ref={ref} className='new-product__dropdown-items'>
              <div
                className="new-product__dropdown-item"
                onClick={(e: React.MouseEvent<HTMLDivElement>) => handleOnClick(e.currentTarget.innerText, dropDownName)}
              >
                {option}
              </div>
            </div>
          ))}
        </SimpleBarReact>

      </div>
    </>
  );
};